.footer{
    margin-top: 9.2rem;
    border-top: 0.15rem solid $principal;
    margin-bottom: 5rem;
    @include tablet{
        margin-top: 13.2rem;
    }
}
.footer-contenedor{
    margin-top: 5.2rem;
    @include grid(1,3rem);

    @include tablet{
        margin-top: 10.2rem;
        @include grid(2,20rem);
    }
}

.footer-iz{

    h1{
        text-transform: uppercase;
        font-weight: $bold;
        text-align: left;
        margin: 0 0 3rem 0;
    }
    p{
        font-size: 1.8rem;
        margin: 0;
        text-align: center;
        @include tablet{
            text-align: left
        }
    }
}
.footer-de{
    @include grid(3,1rem);
    @include tablet{
        @include grid(3,9.6rem);
    }
    h2{
        margin: 0 0 0.8rem 0;
        color: $secundario;
        font-size: 1.8rem;
        text-align: center;
        @include tablet{
            font-size: 2rem;
            text-align: left;
        }
    }

    a{
        font-size: 1.4rem;
        text-align: center;
        @include tablet{
            font-size: 1.8rem;
            text-align: left;
        }
    }
}

.jungle, .azienda{
    @include grid(1,1.2rem);
}
.social{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;

   
}