body {
  font-family: "Syne", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #003A1B;
}

.contenedor {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto;
}

p {
  color: #F8F5ED;
  font-size: 2rem;
}

a {
  text-decoration: none;
  color: #F8F5ED;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Syne", sans-serif;
  margin: 25px 0;
  font-weight: 300;
  text-align: center;
  color: #F8F5ED;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.header {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header {
    margin-top: 5rem;
  }
}

.header-contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-contenedor h1 {
  margin: 0;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .header-contenedor h1 {
    font-size: 2.4rem;
  }
}

span {
  color: #CCFF33;
}

.nav-navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .nav-navegacion {
    flex-direction: row;
    align-items: center;
    gap: 5rem;
  }
}

.nav-control {
  display: flex;
  align-items: center;
  gap: 2.1rem;
}

.btn-search {
  cursor: pointer;
}

.cartera-bg {
  height: 100%;
  width: 100%;
  border-radius: 50%;
  padding: 0.8rem;
  background-color: #006400;
  cursor: pointer;
}

