.header-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  background-color: rgba(23, 23, 23, 0.7960784314);
  position: fixed;
  top: 0;
  gap: 5rem;
  width: 100%;
  padding: 0;
  height: 100%;
  overflow-y: auto;
  left: 100%;
  transition: left 0.5s;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .header-nav {
    flex-direction: row;
    align-items: center;
    gap: 20rem;
    background-color: transparent;
    position: relative;
    top: 0;
    width: auto;
    padding: 0;
    left: 0;
    overflow: hidden;
    height: auto;
  }
}

.movile-menu {
  display: block;
  width: 3.5rem;
}
@media (min-width: 768px) {
  .movile-menu {
    display: none;
  }
}

.mostrar {
  left: 0;
}

.barra-fixed {
  position: fixed;
}

.fijar-body {
  overflow: hidden;
}

