.header {
  margin-top: 2rem;
  @include tablet {
    margin-top: 5rem;
  }
}
.header-contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
  h1 {
    margin: 0;
    text-transform: uppercase;
    font-weight: $bold;
    font-size: 2rem;
    @include tablet {
      font-size: 2.4rem;
    }
  }
}
span {
  color: $secundario;
}
.nav-navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;

  font-size: 1.6rem;
  font-weight: $bold;
  text-transform: uppercase;
  @include tablet {
    flex-direction: row;
    align-items: center;
    gap: 5rem;
  }
}
.nav-control {
  display: flex;
  align-items: center;
  gap: 2.1rem;
}

.btn-search{
    cursor: pointer;
}

.cartera-bg {
  height: 100%;
  width: 100%;
  border-radius: 50%;
  padding: 0.8rem;
  background-color: $bacground_2;
  cursor: pointer;
}

/*navegacion*/
.header-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  background-color: #171717cb;
  position: fixed;
  top: 0;
  gap: 5rem;

  width: 100%;
  padding: 0;

  height: calc(100%);
  overflow-y: auto;

  left: 100%;
  transition: left 0.5s;

  font-size: 1.8rem;
  @include tablet {
    flex-direction: row;
    align-items: center;
    gap: 20rem;
    background-color: transparent;
    position: relative;
    top: 0;
    width: auto;
    padding: 0;
    left: 0;
    overflow: hidden;
    height: auto;
  }
}

.movile-menu {
  display: block;
  width: 3.5rem;
  @include tablet {
    display: none;
  }
}

.mostrar {
  left: 0;
}

.barra-fixed {
  position: fixed;
}

.fijar-body{
  overflow: hidden;
}