.carrucelTest {
  margin-top: 2.6rem;
  @include tablet{
    margin-top: 8.6rem;
  }
}
.carrucelTest-contenedor {
}

.contenedor-cardTest {
  @include grid(3, 3rem);
}

.card-test {
  
  @include grid(1,3rem);

  background-color: $bacground_2;
  padding: 2.4rem;
  border: 0.2rem solid $principal;

  width: 100%;

  .comilla {
    width: 3.333rem;
  }
  p {
    margin: 0;
  }
}

.info-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.perfil {
  display: flex;
  align-items: center;
  gap: 1.4rem;
  img {
    width: 5rem;
    height: 5rem;
  }
}
.puntuacion {
  display: flex;
  gap: 1.1rem;
  align-items: center;
  justify-content: center;
}
