.buttonhome {
    margin-top: 3rem;
    @include tablet{
      margin-top: 6.3rem;
    
  }
}

.buttonhome-contenedor {
  @include grid(1, 2rem);
  @include tablet {
    @include grid(3, 10rem);
  }
}
.cardc {
  text-align: justify;

  p {
    font-size: 1.4rem;
    @include tablet {
      font-size: 1.8rem;
      margin-top: 1.4rem;
    }
  }
}

.card-button {
  background-color: $bacground_2;
  border: 0.1rem solid $principal;
  border-radius: 4.3rem;

  h2 {
    font-size: 1.8rem;
    font-weight: $bold;
    color: $secundario;
    text-transform: uppercase;
    margin: 1rem;

    @include tablet {
      font-size: 2rem;
      margin: 1rem;
    }
  }
}
