.antesFooter {
  margin-top: 4rem;

  @include tablet {
    margin-top: 15rem;
  }
}
.antesFooter-contenedor {

    h1{
        font-size: 3.4rem;
        font-weight: $bold;
        margin: 0 0 2rem 0;
        text-transform: uppercase;

        @include tablet{
            font-size: 6.4rem;
        }
    }
    .subtitle{
        text-align: center;
        font-size: 1.8rem;
        margin: 0 0 4rem 0;
    }
}

.barra {
    display: flex;
    justify-content: right;
    margin: 0;
    
    border: 0.1rem solid $principal;
    border-radius: 5.6rem;
    padding: .6rem;

    @include tablet{
        margin: 0 40rem;
    }
}
.barra-botton {
    background-color: $secundario;
    padding: 1rem 2.8rem;
    border-radius: 5.6rem;
    p{
        margin: 0;
        font-size: 1.8rem;
        text-transform: uppercase;
        font-weight: $bold;
        color: $background;
    }
}
