.vitrina{
    margin-top: 4rem;
    
    @include tablet{
        margin-top: 20rem;
    }
}

.vitrina-contenedor{
    @include grid(1,4rem);
    @include tablet{
        @include grid(2,5.6rem);
    }
}
.vitrina-iz{
   
    h1{
        font-size: 3.8rem;
        font-weight: $bold;
        line-height: 5.6rem;
        letter-spacing: 1%;
        text-align: center;
        text-transform: uppercase;
        margin: 0 0 2.5rem 0;

        @include tablet{
            font-size: 4rem;
            text-align: left;
        }
    }
    p{
        font-size: 1.4rem;
        margin: 0 0 3.3rem 0;
        text-align: center;
        @include tablet{    
            font-size: 1.8rem;
            text-align: left;
        }
    }
    .vitrinabtn{
        background-color: $secundario;
        margin-bottom: 4.9rem;
        display: flex;
        justify-content: center;
        padding: 0;
        margin: 0 2rem 2rem 2rem;
        border-radius: 3.7rem;
        
        @include tablet{
            display: inline-block;
            padding: 1.5rem 2.4rem;
            margin-bottom: 4.9rem;
        }
        p{
            font-size: 1.8rem;
            font-weight: $bold;
            text-transform: uppercase;
            color: $background;
            margin: 0;
        }
    }
    img{
        border: 0.2rem solid $principal;
    }
}

.vitrina-de{

    img{
        border: 0.2rem solid $principal;
    }
}

.cualidades{
    display: flex;
    justify-content: center;
    @include tablet{
        justify-content: space-between;
    }
    
}
.cuali{
    
    h2  {
        font-size: 3.4rem;
        font-weight: $bold;
        text-align: center;
        margin: 0 0 1.4rem 0;
        color: $secundario;
        @include tablet{
            font-size: 4.4rem;
            text-align: left;
        }
    }
    p{
        margin: 0 0 4rem 0;
        font-size: 1.8rem;
        font-weight: $semiBold;
        text-align: center;
        @include tablet{
            font-size: 2rem;
            text-align: left;
            margin: 0 0 3.6rem 0;
        }
    }
    
}