.carrucel {
  margin-top: 3rem;
  @include tablet {
    margin-top: 11.6rem;
  }
}

.contenedor-ccard {
  @include grid(3, 5rem);
}

.carrucel-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.ccard-foto {
  width: 100%;
  height: 100%;
  background-color: $bacground_2;
  padding: 4rem;
  border: 0.1rem solid $principal;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 26.8rem;
  }
}
.ccard-text {
  display: flex;
  flex-direction: column;
  align-items:center;
  @include tablet {
    align-items: flex-start;
  }
  h3 {
    margin: 2rem 0 0 0;
    font-size: 2rem;
    font-weight: $semiBold;
    line-height: 2.6rem;
    color: $secundario;
  }
  h2 {
    margin: 1.1rem 0 0 0;
    font-size: 2.6rem;
    font-weight: $bold;
  }
  p {
    margin: 0.6rem 0 0 0;
    font-size: 1.8rem;
    font-weight: $semiBold;
  }
}

.btn-control {
  width: 5rem;
  height: 5rem;
  background-color: $secundario;
  border-radius: 50%;
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
