html {
    box-sizing: border-box;
    font-size: 62.5%;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  body {
    font-family: $fuente-principal;
    font-size: 1.6rem;
    line-height: 1.8;
    background-color: $background;
  }
  
  .contenedor {
    width: 95%;
    max-width: 130rem;
    margin: 0 auto;
  }
  
  p {
    color: $principal;
    font-size: 2rem;
  }
  a {
    text-decoration: none;
    color: $principal;
  }
  
  img,
  picture {
    width: 100%;
    display: block;
  }
  
  h1,
  h2,
  h3 {
    font-family: $fuente-principal;
    margin: calc($separacion / 2) 0;
    font-weight: 300;
    text-align: center;
    color: $principal;
  }
  
  h1 {
    font-size: 3.8rem;
  }
  h2 {
    font-size: 3.4rem;
  }
  h3 {
    font-size: 3rem;
  }
  h4 {
    font-size: 2.6rem;
  }