.nextcarrucel{
    margin-top: 5rem;
    @include tablet{
        margin-top: 9.7rem;
    }
}
.nextcarrucel-contenedor{

    h1{
        font-size: 3.8rem;
        font-weight: $bold;
        line-height: 4.7rem;
        letter-spacing: 0.0001rem;
        text-transform: uppercase;
        margin: 0;
        text-align: center;
        @include tablet{
            font-size: 4.8rem;
            line-height: 6.7rem;
        }
    }
    p{
        font-weight: 1.8rem;
        text-align: center;
        font-weight: $regular;
        padding: 0;
        margin-top: 1.8rem;
        @include tablet{
            margin-top: 3.8rem;
            padding: 0 26rem;
        }
    }
}