.beforCarrucel{
    margin-top: 2rem;
    @include tablet{
        margin-top: 11.0rem;
    }
}

.beforCarrucel-contenedor{

    @include grid(1,2rem);
    @include tablet{
        @include grid(2,7.2rem);
    }

    h1{
        margin: 0;
        font-size: 3.8rem;
        font-weight: $bold;
        text-transform: uppercase;
        text-align: center;
        line-height: 4.1rem;

        @include tablet{
            font-size: 4.8rem;
            text-align: left;
            line-height: 6.7rem;
        }

    }
    p{
        margin: 0;
        font-size: 1.4rem;
        font-weight: $regular;
        line-height: 1.8rem;
        margin: 0 ;
        text-align: center;

        @include tablet{
            margin: 0 6rem;
            text-align: left;
            line-height: 3.2rem;
            font-size: 1.8rem;
        }
    }
}