
@mixin grid($colum, $gap){
  display: grid;
  grid-template-columns: repeat($colum, 1fr);
  gap: $gap;
}

/** Media Queries **/
@mixin telefono {
  @media (min-width: #{$nivel2}) {
      @content;
  }
}

@mixin tablet {
  @media (min-width: #{$nivel3}) {
      @content;
  }
}

@mixin desktop {
  @media (min-width: #{$nivel4}) {
      @content;
  }
}
