.home {
  margin-top: 4rem;
  @include tablet {
    margin-top: 8rem;
  }
}

.home-contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  h1 {
    font-weight: $bold;
    font-size: 3.4rem;
    line-height: 4.1rem;
    align-items: center;
    text-transform: uppercase;
    margin: 0;

    @include tablet {
      font-size: 6.4rem;
      line-height: 8.1rem;
    }
  }
  .title2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0rem;

    @include tablet {
      gap: 1rem;
      flex-direction: row;
    }

    img {
      width: 9.2rem;
    }
  }

  .fondo-parrafo {
    background-image: url(../img/portada_11.webp);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;

    @include tablet {
        background-image: none;
    }
  }
  p {
    font-size: 1.8rem;
    margin: 2rem 0 2rem 0;
    text-align: center;
    background-color: #00000075;

    @include tablet {
      margin: 2rem 0 0 0;
      background-color: transparent;
    }
  }

  picture {
    margin-top: 5rem;
    display: none;
    @include tablet {
      display: block;
    }
    img {
      height: 55.6rem;
    }
  }
}
